import {AppBar, Box, Breadcrumbs, IconButton, Toolbar, Typography, useTheme} from "@mui/material";
import {Brightness4, Brightness7, NavigateNext} from "@mui/icons-material";
import {useColorModeContext} from "../Theme";

function TopBar({openCodes,  setExpanded}: { setExpanded: any, openCodes: { [key: string]: string }[] }) {
    const colorMode = useColorModeContext()
    const theme = useTheme()
    return (
        <AppBar position="fixed" elevation={2} sx={{height: {mobile: 100, laptop: 70}, justifyContent: 'center', bgcolor: 'primary.dark'}}>
            <Toolbar sx={{width: 1, display: 'flex', flexDirection: 'column', justifyContent: 'center'}}>
                <Box sx={{width: 1, display: {mobile: 'flex', laptop: "none"}, alignItems: 'center'}}>
                    <Typography variant="h5" sx={{
                        color: 'primary.text',
                        fontWeight: 600
                    }}>Dewey Decimal
                        Classification</Typography>
                    <Box sx={{flex: 1, display: 'flex'}}>
                        <IconButton style={{marginLeft: 'auto'}} onClick={colorMode.toggleColorMode} color="inherit">
                            {theme.palette.mode === 'dark' ? <Brightness7/> : <Brightness4/>}
                        </IconButton>
                    </Box>
                </Box>
                <Box sx={{width: 1, display: 'flex', alignItems: 'center'}}>
                    <Breadcrumbs sx={{flex: 4}}
                                 separator={<NavigateNext fontSize="medium" sx={{color: 'primary.text'}}/>}>
                        <Typography variant="h5" sx={{
                            color: 'primary.text',
                            fontWeight: 600,
                            display: {mobile: 'none', laptop: "inherit"}
                        }}>Dewey Decimal
                            Classification</Typography>
                        {openCodes.map(item =>
                            <Typography
                                variant="crumbs"
                                sx={{color: 'primary.text', "&:hover": {color: 'primary.hover'}}}
                                onClick={() => setExpanded(item.code)}
                            >{item.title}</Typography>
                        )}
                    </Breadcrumbs>
                    <Box sx={{flex: 1, display: {mobile: 'none', laptop: "flex"}}}>
                        <IconButton style={{marginLeft: 'auto'}} onClick={colorMode.toggleColorMode} color="inherit">
                            {theme.palette.mode === 'dark' ? <Brightness7/> : <Brightness4/>}
                        </IconButton>
                    </Box>
                </Box>
            </Toolbar>
        </AppBar>
    )
}

export default TopBar